import { Form } from "./admin/columns/form";
import { EventConstants } from "./constants";
import { initAdminColumnsGlobalBootstrap } from "./helpers/admin-columns";
// @ts-ignore
import $ from 'jquery';
import ColumnConfigurator from "./admin/columns/column-configurator";
initAdminColumnsGlobalBootstrap();
new ColumnConfigurator();
document.addEventListener('DOMContentLoaded', function () {
    new SaveButtons();
    var formElement = document.querySelector('#listscreen_settings');
    if (formElement) {
        AdminColumns.Form = new Form(formElement, AdminColumns.events);
    }
});
AdminColumns.events.addListener(EventConstants.SETTINGS.FORM.LOADED, function (form) {
    document.querySelectorAll('.add_column').forEach(function (el) {
        el.addEventListener('click', function (e) {
            e.preventDefault();
            form.createNewColumn();
        });
    });
    document.querySelectorAll('a[data-clear-columns]').forEach(function (el) {
        el.addEventListener('click', function () { return form.resetColumns(); });
    });
});
AdminColumns.events.addListener(EventConstants.SETTINGS.FORM.SAVING, function () {
    document.querySelector('#cpac .ac-admin').classList.add('saving');
});
AdminColumns.events.addListener(EventConstants.SETTINGS.FORM.SAVED, function () {
    document.querySelector('#cpac .ac-admin').classList.remove('saving');
    document.querySelector('#cpac .ac-admin').classList.add('stored');
});
AdminColumns.events.addListener(EventConstants.SETTINGS.FORM.LOADED, function (form) {
    var $form = $(form.getElement());
    if ($form.hasClass('ui-sortable')) {
        $form.sortable('refresh');
    }
    else {
        $form.sortable({
            items: '.ac-column',
            handle: '.column_sort'
        });
    }
});
AdminColumns.events.addListener(EventConstants.SETTINGS.FORM.LOADED, function (form) {
    document.querySelectorAll('a[data-clear-columns]').forEach(function (el) {
        el.addEventListener('click', function () { return form.resetColumns(); });
    });
});
var SaveButtons = /** @class */ (function () {
    function SaveButtons() {
        this.elements = document.querySelectorAll('.sidebox a.submit, .column-footer a.submit');
        this.init();
    }
    SaveButtons.prototype.init = function () {
        var _this = this;
        AdminColumns.events.addListener(EventConstants.SETTINGS.FORM.READY, function (form) {
            _this.elements.forEach(function (el) {
                el.addEventListener('click', function (e) {
                    e.preventDefault();
                    _this.disable();
                    form.submitForm();
                });
            });
        });
        AdminColumns.events.addListener(EventConstants.SETTINGS.FORM.SAVED, function () { return _this.enable(); });
    };
    SaveButtons.prototype.disable = function () {
        this.elements.forEach(function (el) { return el.setAttribute('disabled', 'disabled'); });
    };
    SaveButtons.prototype.enable = function () {
        this.elements.forEach(function (el) { return el.removeAttribute('disabled'); });
    };
    return SaveButtons;
}());
